@import url("https://fonts.googleapis.com/css2?family=Jost:wght@400;500;700&family=Lato&family=Noto+Sans+JP&family=Open+Sans&family=Roboto:ital,wght@0,100;1,900&display=swap");

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  font-family: "Jost", Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
ul,
ol,
p,
body {
  margin: 0;
  padding: 0;
}

input,
button,
a {
  outline: none;
}

button,
input {
  font-size: 16px;
}

a {
  text-decoration: none;
}

body {
  background-color: black;
  color: #fff;
}

@font-face {
  // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом)
  font-family: "Bison";
  // адрес шрифта и тип
  src: url("../font/Bison-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@media screen and (max-width: 767px) {
  html {
    min-width: 360px;
  }
}
